* {
  box-sizing: border-box;

}

body {
font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
	margin: 0;
	padding: 0;
}

#search-form {
	display: flex;
	justify-content: center;
	position: fixed;
	width: 100%;
	margin-bottom: 20px;
	z-index: 1;
	background-color: #d234eb;
  font: inherit;
  padding: 8px;
  margin-bottom: 16px;
	height: 45px;
}

#search-form input:focus {
	box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2), inset 0 1px 2px rgba(0, 0, 0, 0.4);
	background: #E8E8E8;
}

#search-form input{
	width: 250px;
	height: 30px;
	border-radius: 7px;
	border: none;
}

#search-form button{
	width: 25px;
	height: 25px;
	position: absolute;
	margin-left: 218px;
	margin-top: 2px;
border: none;
	background: url(https://lh4.googleusercontent.com/-b-5aBxcxarY/UAfFW9lVyjI/AAAAAAAABUg/gQtEXuPuIds/s13/go.png) 50% no-repeat;
}

#search-form button:hover{
	opacity: 0.5;
}

.load-more{
display: block;
visibility: hidden;
	margin-left: auto;
		margin-right: auto;
	font: inherit;
		padding: 8px;
		max-width: 320px;
}

.gallery{
padding-top: 60px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
}

.photo-card {
	height: 270px;
	width: 315px;
	margin-bottom: 20px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
	border-radius: 10px;

}
.info {
	padding: 10px;
	display: flex;
	justify-content: space-around;
}
.info-item{
	display: flex;
	width: auto;
	flex-wrap: wrap;
	margin: 0;
	justify-content: center;
}

img{
	border-radius: 10px 10px 0 0;
	height: 200px;
	width: 315px;
}
